import React, { useState, useEffect } from 'react';
import 'mind-ar/dist/mindar-image.prod.js';
import 'aframe';
import 'aframe-extras';
import 'mind-ar/dist/mindar-image-aframe.prod.js';
import './App.css';

//  Componentes AFRAME
import './components/aframe-components/go-to-url';
import './components/aframe-components/open-modal-information';
import './components/aframe-components/mind-target-manager';
import './components/aframe-components/scene-loader';

//  Contexto
import { ContextUIProvider } from './context/ContextUI';

//  Componentes REACT
import MindARViewer from './mindar-viewer';
import StartModal from './components/react-components/StartModal/StartModal';
import ModalInformation from './components/react-components/ModalInformation/ModalInformation';
import ModalDiscount from './components/react-components/ModalDiscount/ModalDiscount';
import ModalInstructions from './components/react-components/ModalInstructions/ModalInstructions';

function App() {
  const [started, setStarted] = useState(false);
  const [arReady, setArReady] = useState(false);
  const [sceneLoaded, setSceneLoaded] = useState(false);

  const OnLoadedScene = (e) => {
    setSceneLoaded(true);
  }

  useEffect(() => {
    document.addEventListener('loaded_scene_aframe', OnLoadedScene)
    return () => document.removeEventListener("loaded_scene_aframe", OnLoadedScene);
  }, []);

  return (
    <ContextUIProvider>
      <div className="App">

        <div className="container">
          <MindARViewer started={started} setArReady={setArReady} />
        </div>

        <div className="container">
          <img className="o-logo-header"
            src={process.env.PUBLIC_URL + '/assets/images/Ley_6.png'}
            width="4rem" alt=""
            style={{ position: 'absolute', top: '.5rem', left: '1rem', width: '4rem' }} />

          <ModalInstructions />
          {/* <ModalInformation /> */}
          {/* <ModalDiscount /> */}
          <StartModal sceneLoaded={sceneLoaded} arReady={arReady} setStarted={setStarted} />
        </div>

      </div>
    </ContextUIProvider>
  );
}

export default App;
