import React, { useEffect } from 'react';
import Dialog from '@mui/material/Dialog';
import Slide from '@mui/material/Slide';
// import ViewInArIcon from '@mui/icons-material/ViewInAr';
import CardGiftcardIcon from '@mui/icons-material/CardGiftcard';
import LoadingButton from '@mui/lab/LoadingButton';
import { Box, Button, Container, Grid, Link, TextField, Typography } from '@mui/material';

// Componentes
import Copyright from '../Copyright/Copyright';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

export default function StartModal({ sceneLoaded, arReady, setStarted }) {
    const [open, setOpen] = React.useState(true);
    const [loading, setLoading] = React.useState(false);

    useEffect(() => {
        if (arReady) {
            setOpen(false);
        }
    }, [arReady]);

    const handleClose = () => {
        setLoading(true);
        setStarted(true);
    };

    return (

        <Dialog
            fullScreen
            open={open}
            onClose={handleClose}
            TransitionComponent={Transition}
            PaperProps={{
                style: {
                    backgroundColor: 'white',
                    boxShadow: 'none',
                },
            }}
        >
            <div className="background">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
            </div>
            <Container maxWidth="sm"
                sx={{
                    display: 'flex',
                    height: '100%',
                    width: '90%',
                    flexDirection: 'column',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    padding: '2rem 0 0 0',
                    zIndex: 2
                }}>
                <img className="o-logo-header"
                    src={process.env.PUBLIC_URL + '/assets/images/Ley_1.jpeg'}
                    height="250" alt="" />

                <Box sx={{ py: 1 }}>

                    <Typography
                        align="center"
                        color="textPrimary"
                        component={'div'} variant={'body1'}
                    >
                        <Box sx={{ fontWeight: 'bold', m: 1, fontSize: '2rem', }}>Leeey</Box>
                        Feliz cumpleaños a la mujer más fuerte y luchadora. 🎂🎂
                        <br/>
                        Espero que sea un día lleno de cosas bonitas y que en adelante vengan muchos éxitos 
                        y bendiciones. 🥳🥳 🎉🎉 
                        <br/>
                        Gracias por formar parte de mi vida y ser mi "maestra" en el camino. 😁😁😁
                        <br/>
                        Un pequeño detalle hecho a mi manera 😬😬😬

                    </Typography>

                    <LoadingButton
                        // className='interactable-ui'
                        disabled={!sceneLoaded}
                        sx={{ mt: 3 }}
                        fullWidth
                        size='large'
                        loading={loading}
                        loadingPosition="start"
                        startIcon={<CardGiftcardIcon />}
                        variant="contained"
                        onClick={handleClose}
                    >
                        {sceneLoaded ? 'Abrir regalo' : 'Cargando...'}
                    </LoadingButton>
                </Box>

                {/* <br></br> */}
                <Box sx={{ py: 1, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                    <img className="o-logo-header"
                        src={process.env.PUBLIC_URL + '/assets/images/Ley_5.png'}
                        height="100" alt=""
                        style={{ marginBottom: '1rem' }} />

                    <Copyright></Copyright>
                </Box>

            </Container>
        </Dialog>
    );
}