const AFRAME = window.AFRAME;

AFRAME.registerComponent('go-to-url', {
    schema: {
        url: {type: 'string', default: ''}
    },

    init: function () {

        const url = this.data.url;

        this.el.addEventListener('click', function (evt) {
            window.open(url, '_blank');
        });

    }
});