import React, { useState, useEffect } from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Slide from '@mui/material/Slide';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import ViewInArIcon from '@mui/icons-material/ViewInAr';
import Typography from '@mui/material/Typography';

export default function ModalInstructions() {
    const [open, setOpen] = useState(true);

    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('md'));

    const handleClose = () => {
        setOpen(false);
    };
    const openModal = (e) => {
        setOpen(true);
    }

    useEffect(() => {
        document.addEventListener('close_instructions_modal', handleClose)
        return () => document.removeEventListener("close_instructions_modal", handleClose);
    }, []);


    return (
        <Dialog
            fullScreen={false}
            open={open}
            aria-labelledby="responsive-dialog-title"
            PaperProps={{
                style: {
                    backgroundColor: 'transparent',
                    boxShadow: 'none',
                },
            }}
        >
            <Box
                sx={{
                    width: 300,
                    height: 300,
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center',
                    alignItems: 'center',
                    color: 'white',
                    textAlign: 'center'
                }}
            >
                {/* <ViewInArIcon sx={{ fontSize: '7rem', mb: 3 }} /> */}
                <img className="o-logo-header"
                    src={process.env.PUBLIC_URL + '/assets/targets/collage.jpeg'}
                    width="7rem" alt=""
                    style={{ width: '80%', maxHeight:'60%', objectFit:'contain', opacity:0.3, marginBottom:'1rem' }} />

                <Typography variant="button" display="block" gutterBottom>
                    Abre la imágen que recibiste en WhatsApp y apunta la cámara hacia ella
                </Typography>
            </Box>


        </Dialog>
    );
}