const AFRAME = window.AFRAME;

AFRAME.registerComponent('open-modal-information', {
    schema: {
        title: {type: 'string', default: 'Título'},
        information: {type: 'string', default: 'Sin información'},
    },

    init: function () {

        const data = this.data;

        const OpenModalEvent = new CustomEvent('open_information_modal', {
            detail: {
                title: data.title,
                information: data.information,
            }
        });
        
        this.el.addEventListener('click', function (evt) {
            document.dispatchEvent(OpenModalEvent);
        });

    }
});

AFRAME.registerComponent('open-modal-discount', {

    init: function () {

        const OpenModalEvent = new CustomEvent('open_discount_modal', {
        });
        
        this.el.addEventListener('click', function (evt) {
            document.dispatchEvent(OpenModalEvent);
        });

    }
});