const AFRAME = window.AFRAME;
const closeInstructionsEvent = new CustomEvent('close_instructions_modal', {});

AFRAME.registerComponent('mind-target-manager', {
    schema: {
    },

    init: function () {

        const marker_image =  document.querySelector('#plane-image-1'); 
        const image_1 =  document.querySelector('#a-image-1');
        const image_2 =  document.querySelector('#a-image-2');
        const image_3 =  document.querySelector('#a-image-3');
        const image_4 =  document.querySelector('#a-image-4');
        const image_5 =  document.querySelector('#a-image-5');

        const imagesArray = [ marker_image , image_1, image_2, image_3 , image_4, image_5]
        let currentImage = marker_image;
        let oldImage = image_5;

        let counter = 0
        let isEnabledChange = false;

        const timeout = () => setTimeout(() =>{
            oldImage.setAttribute('visible', false)
            if(!isEnabledChange) return;

            ChangeImageFunction();
        }, 3000);

        function ChangeImageFunction(){

            oldImage = currentImage;

            counter = counter >= imagesArray.length -1 ? 0 : counter +1;
            currentImage = imagesArray[counter];

            currentImage.setAttribute('visible', true);
            oldImage.emit('close');
            currentImage.emit('spawn');

            // console.log(currentImage.getAttribute('id'))

            timeout();
        }

        
        
        this.el.addEventListener('targetFound', event => {
            document.dispatchEvent(closeInstructionsEvent);

            currentImage.setAttribute('visible', true);
            currentImage.emit('spawn');
            
            isEnabledChange = true;
            timeout();

        });
        this.el.addEventListener('targetLost', event => {
            isEnabledChange = false;
        });

    }
});
