import React, { useEffect, useRef } from 'react';

export default ({ started, setArReady }) => {
  const sceneRef = useRef(null);

  useEffect(() => {
    const sceneEl = sceneRef.current;
    const arSystem = sceneEl.systems["mindar-image-system"];
    sceneEl.addEventListener('renderstart', () => {
      //arSystem.start(); // start AR 
    });

    sceneEl.addEventListener("arReady", (event) => {
      setArReady(true);
    });
    return () => {
      arSystem.stop();
    }
  }, []);

  useEffect(() => {
    const sceneEl = sceneRef.current;
    const arSystem = sceneEl.systems["mindar-image-system"];
    if (started) {
      arSystem.start();
    }
  }, [started]);

  return (
    <a-scene
      scene-loader
      class='interactable-ui'
      ref={sceneRef}
      mindar-image={`
            imageTargetSrc: ${process.env.PUBLIC_URL + '/assets/targets/targets.mind'};
            autoStart: false;
            uiScanning: #scanning-overlay; 
            uiError: yes;
            filterMinCF:0.001; 
            filterBeta: 100;
            warmupTolerance: 1
            missTolerance: 2"`}
      color-space="sRGB"
      renderer="colorManagement: true, physicallyCorrectLights"
      vr-mode-ui="enabled: false"
      device-orientation-permission-ui="enabled: false">

      <a-assets>

        <a-asset-item id="bearModel" src="https://cdn.jsdelivr.net/gh/hiukim/mind-ar-js@1.2.0/examples/image-tracking/assets/band-example/bear/scene.gltf"></a-asset-item>
        <img
          id="card_image"
          src={process.env.PUBLIC_URL + '/assets/targets/collage.jpeg'}
        />

        <img
          id="image_1"
          src={process.env.PUBLIC_URL + '/assets/images/Image_1.jpeg'}
        />

        <img
          id="image_2"
          src={process.env.PUBLIC_URL + '/assets/images/Image_2.jpeg'}
        />

        <img
          id="image_3"
          src={process.env.PUBLIC_URL + '/assets/images/Image_3.jpeg'}
        />

        <img
          id="image_4"
          src={process.env.PUBLIC_URL + '/assets/images/Image_4.jpeg'}
        />

        <img
          id="image_5"
          src={process.env.PUBLIC_URL + '/assets/images/Image_5.jpeg'}
        />

      </a-assets>

      <a-camera
        position="0 0 0"
        look-controls="enabled: false"
        cursor="fuse: false; rayOrigin: mouse;"
        raycaster="far: 10000; objects: .clickable">
      </a-camera>

      <a-entity mindar-image-target="targetIndex: 0"
        mind-target-manager>

        <a-gltf-model
          rotation="0 0 0 "
          position="-0.6 -0.3 0.25"
          scale="0.05 0.05 0.05"
          src="#bearModel"
          animation-mixer="clip: *;">
        </a-gltf-model>

        <a-entity 
          text="value: Feliz Cumpleee; font: mozillavr; color:white; align:center" 
          scale="2 2 2" 
          position="0 0.7 0"
          animation="property: scale; dir: alternate; dur: 1000;
                           easing: easeInSine; loop: true; from: 2.8 2.8 2.8; to: 2.2 2.2 2.2">
        </a-entity>

        <a-plane id="plane-image-1" visible="false" src="#card_image" width="1" height="1"
          animation__spawn="property: components.material.material.opacity; from: 0; to: 1; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: spawn;"
          animation__close="property: components.material.material.opacity; from: 1; to: 0; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: close;">
        </a-plane>

        <a-plane id="a-image-1" visible="false" src="#image_1" width="1" height="1"
          animation__spawn="property: components.material.material.opacity; from: 0; to: 1; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: spawn;"
          animation__close="property: components.material.material.opacity; from: 1; to: 0; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: close;">
        </a-plane>

        <a-plane id="a-image-2" visible="false" src="#image_2" width="1" height="1"
          animation__spawn="property: components.material.material.opacity; from: 0; to: 1; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: spawn;"
          animation__close="property: components.material.material.opacity; from: 1; to: 0; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: close;">
        </a-plane>

        <a-plane id="a-image-3" visible="false" src="#image_3" width="1" height="1"
          animation__spawn="property: components.material.material.opacity; from: 0; to: 1; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: spawn;"
          animation__close="property: components.material.material.opacity; from: 1; to: 0; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: close;">
        </a-plane>

        <a-plane id="a-image-4" visible="false" src="#image_4" width="1" height="1"
          animation__spawn="property: components.material.material.opacity; from: 0; to: 1; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: spawn;"
          animation__close="property: components.material.material.opacity; from: 1; to: 0; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: close;">
        </a-plane>

        <a-plane id="a-image-5" visible="false" src="#image_5" width="1" height="1"
          animation__spawn="property: components.material.material.opacity; from: 0; to: 1; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: spawn;"
          animation__close="property: components.material.material.opacity; from: 1; to: 0; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: close;">
        </a-plane>

      </a-entity>

    </a-scene>
  )
}
